.timer-widget .timer-slider .timer-range {
  display: inline-block;
  width: 100%;
  transform: rotate(90deg);
  background: linear-gradient(100deg, rgba(0, 0, 0, 0.1) 0%, rgb(255, 255, 255) 35%, rgba(255, 255, 255, 0) 100%);
  box-shadow: 0px 0px 1px #353535;
  -webkit-appearance: none;
  appearance: none;
  -moz-appearance: none;
  outline: none;
  height: 2px;
  position: relative;
}
.timer-widget .timer-slider .timer-range::before {
  background-image: url(https://static.iviva.com/images/Udhayimages/sun.png);
  content: "";
  width: 40px;
  height: 40px;
  display: block;
  background-size: 100% auto;
  position: absolute;
  top: -18px;
  left: -50px;
  background-repeat: no-repeat;
  background-position: center center;
}
.timer-widget .timer-slider .timer-range::after {
  background-image: url(https://static.iviva.com/images/Udhayimages/moon.png);
  content: "";
  width: 35px;
  height: 35px;
  display: block;
  background-size: 100% auto;
  position: absolute;
  bottom: -20px;
  right: -45px;
  background-repeat: no-repeat;
  background-position: center center;
  transform: rotate(160deg);
}
.timer-widget .timer-slider .timer-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #fefefe;
  width: 18px;
